<template>
  <div>
    <v-container class="comprometete__container">
      <title-ribbon :title="'Comprométete con tu Terapia'" />
      <div class="comprometete_container__main">
        <v-row class="comprometete_row__img">
          <v-col>
            <v-img
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/comprometete-con-tu-terapia-con-psicologo"
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="comprometete_row__header">
          <v-col>
            <p>
              El proceso terapéutico es una labor donde, a lo menos, participan
              el paciente y el terapeuta. En este trabajo, resulta fundamental
              el involucramiento del paciente, ya que sin este no hay terapia, y
              si el paciente no asiste a las sesiones y no cumple con lo
              acordado con su terapeuta no hay proceso. Por eso, gran parte del
              éxito de un proceso terapéutico tiene que ver con la motivación
              del paciente, con su perseverancia y compromiso.
            </p>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="comprometete_row__compromiso">
          <v-col class="crc_col1">
            <div class="crc_col1_main">
              <h2>
                Necesitamos tu compromiso para el exito de tu tratamiento:
              </h2>
              <ul>
                <li>
                  Reserva tus sesiones con anticipación, en lo posible
                  manteniendo los mismos días y horarios.
                </li>
                <li>
                  Asiste de manera puntual a las sesiones reservadas. Llega por
                  lo menos 15 minutos antes para efectuar los trámites
                  administrativos de rigor.
                </li>
                <li>
                  No cambies ni suspendas tus sesiones, a menos que sea por
                  motivos de fuerza mayor.
                </li>
                <li>
                  AVISA con anticipación cuando no puedas asistir, para no
                  perjudicar a tu terapeuta y dar la oportunidad de que otro
                  paciente pieda reservar la hora.
                </li>
                <li>
                  Informa de manera veraz y completa acerca de tus problemas y
                  antecedentes de salud que conozcas para tu adecuado
                  diagnóstico y tratamiento.
                </li>
                <li>
                  Sigue la medicación prescrita por tu médico, las indicaciones
                  y/o tareas recomendadas o acordadas con tu terapeuta.
                </li>
              </ul>
            </div>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="comprometete_row__problemas">
          <v-col class="crp_col1">
            <div class="crp_col1_main">
              <h2>Problemas con tu Terapia:</h2>
              <ul>
                <li>
                  Si tienes algún problema con tu terapia, exponlo directamente
                  a tu terapeuta.
                </li>
                <li>
                  Si tienes efectos secundarios con la medicación, comunícate
                  con nuestro Centro.
                </li>
                <li>
                  <div>
                    Si no puedes solucionar el problema con tu terapeuta puedes
                    comunicarlo, por escrito, a la Dirección del Centro vía
                    Buzón de Sugerencia, o por correo electrónico a
                    <strong>centro@terapia.cl</strong>.
                  </div>
                </li>
                <li>
                  Si no estás de acuerdo con la opinión, el diagnóstico y/o la
                  medicación de tu terapeuta, tienes derecho a pedir opinión, ya
                  sea dentro o fuera de nuestro Centro.
                </li>
              </ul>
            </div>
          </v-col>
        </v-row>
        <splitter />
        <message-ribbon v-bind="{ msg: '¡El éxito del tratamiento depende de tí! <br> ¡Comprométete con tu Terapia!' }" />
        <splitter-volver />
      </div>
    </v-container>
  </div>
</template>
<script>
  export default {
    name: 'ComprometeteConTuTerapia',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      MessageRibbon: () => import('@/components/web/base/MessageRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    metaInfo: {
      title: 'Comprométete con tu Terapia',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
